'use strict';

module.exports = function () {
  let init = function () {
    let errorList = document.querySelector('[data-counters-page-tag]');

    if (!errorList) {
      return;
    }

    let domain = errorList.dataset;

    console.log(domain);

    window.alert(domain.countersPageTag);
  };

  return {
    init: init,
  };
}();



